<template>
  <div class="container">
    <div class="empty" v-if="!dataSource.length">从左侧拖拽或点击来添加字段</div>
    <el-form
      v-bind="formConfig"
    >
      <draggable
        :list="dataSource"
        :animation="200"
        ghost-class="ghost"
        group="component"
        class="drag-area"
      >
        <form-box
          v-for="c in dataSource"
          :key="c.id"
          :active="current === c.id"
          @click="$emit('click', c)"
          @copy="copy(c.id)"
          @remove="remove(c.id)"
        >
          <form-item
            v-model="formData[c.key]"
            :config="c"
            :disabled="c.disabled | calcCondition(formData)"
          />
        </form-box>
      </draggable>
    </el-form>
  </div>
</template>

<script>
import draggable from 'vuedraggable';
import FormBox from '@/components/FormBox';
import { uuid, calcCondition } from '@/utils/util';

export default {
  props: ['formConfig', 'current', 'dataSource', 'formData'],
  components: {
    draggable,
    FormBox,
  },
  filters: {
    calcCondition
  },
  data() {
    return {
    };
  },
  methods: {
    remove(id) {
      const idx = this.dataSource.findIndex(d => d.id === id);
      if (this.current === id) {
        this.$emit('update:current', '');
      }
      this.dataSource.splice(idx, 1);
    },
    copy(id) {
      const idx = this.dataSource.findIndex(d => d.id === id);
      const newId = uuid();
      this.dataSource.splice(idx + 1, 0, {
        ...this.dataSource[idx],
        id: newId,
        key: `${this.dataSource[idx].type}_${newId.substr(0, 4)}`
      });
    }
  }
};
</script>

<style lang="scss" scoped>
.container {
  height: 100%;
  position: relative;
  form {
    height: 100%;
    width: 100%;
  }
}
.drag-area {
  width: 100%;
  height: 100%;
}
.empty {
  color: #ccc;
  font-size: 22px;
  text-align: center;
  position: absolute;
  top: 50%;
  width: 100%;
}
.sortable-chosen {
  margin: 2px;
  list-style: none;
  border: 1px dashed hsla(0,0%,66.7%,.5);
  background-color: rgba(236,245,255,.3);
}
</style>



